
import {
  Form, Button, Upload, message, Modal,
} from 'ant-design-vue';
import {
  computed,
  defineAsyncComponent, defineComponent, nextTick, ref, SetupContext, watch,
} from 'vue';
import VueCropper from 'vue-cropperjs';
import 'cropperjs/dist/cropper.css';
/* eslint-disable @typescript-eslint/no-explicit-any */

export default defineComponent({
  components: {
    PageHeader: defineAsyncComponent(() => import('@/components/Public/Exhibition/PageHeader.vue')),
    aForm: Form,
    aFormItem: Form.Item,
    aButton: Button,
    aUpload: Upload,
    // aIcon: defineAsyncComponent(() => import('@/components/BaseIcon.vue')),
    VueCropper,
    aModal: Modal,
  },
  props: {
    /**  */
    disabled: {
      type: Boolean,
      default: false,
    },
    /** title */
    title: {
      type: String,
      default: '',
    },
    /**  */
    oldImgSrc: {
      type: String,
      default: '',
    },
    /**  */
    aspectRatio: {
      type: Number,
      default: 16 / 9,
    },
    /**   api */
    // componentsHeight: {
    //   type: Number,
    //   default: 118,
    // },
    /**  */
    sizeText: {
      type: String,
      default: '210*118',
    },
    /**  M */
    fileSize: {
      type: Number,
      default: 2,
    },
    /**  jpgpng */
    exportType: {
      type: String,
      default: 'png',
    },
    /**  */
    importType: {
      type: Array,
      default: () => ['jpg', 'jpeg', 'png'],
    },
  },
  setup(props, context: SetupContext) {
    const getExportType = (type: string) => {
      if (type && type === 'jpg') return 'image/jpg';
      return 'image/png';
    };

    const modalVisible = ref(false); /**  */
    const choseImg = ref(''); /**  */
    const afterImg = ref(''); /**  */
    const CHeight = 118; // 
    const cropper = ref(null); /** refs */
    const cropperShow = ref(true); /**  */

    /**  */
    const CWidth = computed(() => `${props.aspectRatio * CHeight}px`);
    // eslint-disable-next-line consistent-return
    const beforeUpload = (file: File) => {
      // const arr = ['image/png', 'image/jpeg'];
      const arr = props.importType.map((t) => `image/${t}`);
      if (file.size > props.fileSize * 1024 * 1000) {
        return message.error(`${props.fileSize}M`);
      }
      if (!arr.includes(file.type)) {
        return message.error(`${props.importType}`);
      }
      if (typeof FileReader === 'function') {
        const reader = new FileReader();
        reader.onload = (event: any) => {
          choseImg.value = event.currentTarget.result;

          modalVisible.value = true;
          nextTick(() => {
            /**  */
            (cropper.value as any).replace(event.currentTarget.result);
          });
        };
        reader.readAsDataURL(file);
      }
    };
    const onOk = () => {
      afterImg.value = (cropper.value as any).getCroppedCanvas().toDataURL();

      (cropper.value as any)
        .getCroppedCanvas()
        .toBlob((blob: Blob) => {
          console.log(blob);
          const files = new window.File([blob], '.png', {
            type: getExportType(props.exportType),
          });
          context.emit('upload', files);
          modalVisible.value = false;
        });
    };
    const onCancel = () => {
      choseImg.value = '';
    };

    /**  */
    watch(
      () => props.aspectRatio,
      () => {
        cropperShow.value = false;
        nextTick(() => {
          cropperShow.value = true;
        });
      },
    );

    return {
      beforeUpload,

      CWidth,
      CHeight,
      cropper,
      cropperShow,
      modalVisible,
      choseImg,
      afterImg,
      onOk,
      onCancel,
    };
  },
});
