<div class="img-cover"><a-modal v-model:visible="modalVisible" :title="title" cancelText="" cancelType="link" okText="" @onCancel="onCancel" :keyboard="false" @ok="onOk" :maskClosable="false"><vue-cropper ref="cropper" id="vue-cropper" :src="choseImg" :aspectRatio="aspectRatio" v-if="cropperShow"></vue-cropper></a-modal><div class="left-img" v-if="!oldImgSrc &amp;&amp; !afterImg " :style="{width: CWidth, height: CHeight}"><img src="../assets/img/exhibition/noimg.png"></div><img class="left-img" v-else :src="afterImg || oldImgSrc" :style="{width: CWidth, height: CHeight}"><div class="r-text"><div class="text"><span>{{importType.join('')}}</span><br>
<span>{{ sizeText }}</span><br>
<span>{{fileSize}}M</span></div><a-upload accept=".jpg, .jpeg, .png" :showUploadList="false" :disabled="disabled" :beforeUpload="beforeUpload"><a-button type="dashed"><slot name="prefix"></slot></a-button></a-upload></div></div>